<template>
  <v-flex column class="v-text-field" :class="{ solo }" v-ripple>
    <label :for="id" v-if="label" class="label">{{ label }}</label>

    <input
      ref="input"
      type="text"
      :id="id"
      :class="inputClasses"
      :value="modelValue"
      :placeholder="placeholder"
      @input="onInput"
    />
  </v-flex>
</template>

<script>
import { computed } from 'vue';

let id_counter = 0;

export default {
  name: 'v-text-field',

  emits: ['update:modelValue'],
  props: {
    label: String,
    modelValue: String,
    placeholder: String,

    solo: { type: Boolean, default: false },
    small: { type: Boolean, default: false },
    xSmall: { type: Boolean, default: false },
    large: { type: Boolean, default: false },
    xLarge: { type: Boolean, default: false },
  },

  data() {
    return {
      id: `input${++id_counter}`,
    };
  },

  setup(props) {
    const inputClasses = computed(() => ({
      small: props.small,
      'x-small': props.xSmall,
      large: props.large,
      'x-large': props.xLarge,
    }));

    return { inputClasses };
  },

  methods: {
    onInput(e) {
      this.$emit('update:modelValue', e.target.value);
    },

    blur() {
      this.$refs.input.blur();
    },

    focus() {
      this.$refs.input.focus();
    },

    select() {
      this.$refs.input.select();
    },
  },
};
</script>

<style lang="scss" scoped>
@import "./style.scss";

div.v-text-field {
  position: relative;

  overflow: hidden;
  background-color: darken(white, 8%);
  margin-top: (4 * $unit);

  &.solo {
    border-radius: 4px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  label {
    padding-top: 2 * $unit;
    padding-left: 4 * $unit;

    font-size: 0.75 * $text-unit;
    font-family: Roboto;
    color: lighten(black, 50%);
  }

  input {
    font-size: $text-unit;
    outline: none;

    font-family: Roboto;
    font-weight: normal;

    pointer-events: all;

    border-radius: 4px;

    padding: (2 * $unit) (4 * $unit);

    &.small {
      padding-top: (1 * $unit);
      padding-bottom: (1 * $unit);
      // width: 1.75 * $text-unit;
      // height: 1.75 * $text-unit;
    }

    &.large {
      padding-top: (3 * $unit);
      padding-bottom: (3 * $unit);
      // width: 2.5 * $text-unit;
      // height: 2.5 * $text-unit;
    }
  }
}
</style>
